<template>
    <div class="icons">

        <swiper :options="swiperOption">
            <swiper-slide
                v-for="(item,key) in page" :key="key"
            >
                <div class="icons-item"
                    v-for="(page,key) in item" :key="key"
                >
                    <img :src="page.imgUrl" alt="">
                    <p>{{ page.title }}</p>
                </div>
            </swiper-slide>
            <div class="swiper-pagination" slot="pagination"></div>

        </swiper>

    </div>
</template>

<script>
export default {
    props:["iconsList"],
    data () {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination'
                }
            }
        }
    },

    computed: {
        page() {
            let pages = [];
            this.iconsList.forEach((item,index)=> {
                let idx = Math.floor(index/8);
                if(!pages[idx]) pages[idx] = [];
                pages[idx].push(item);
            })

            return pages;

        }
    }
}
</script>

<style scoped lang="stylus">
@import '~css/common.styl'

.icons {
    width: 100%;
    overflow: hidden;
    padding: 2% 0;
    background: #fff;
}

.icons-item {
    width: 25%;
    padding-bottom: 30%;
    height: 0;
    float: left; 
}

.icons-item img {
    width: 1.1rem;
    height: 1.1rem;
    display: block;
    margin: 0 auto;
    padding-top: .2rem;
}

.icons-item p{
    margin-top: .1rem;
    font-size: .28rem;
    text-align: center;
    color: #212121;
    textOverflow();
}

.icons >>> .swiper-pagination {
    margin-bottom: -18px
}

</style>



