import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let defaultCity = "北京";
defaultCity = localStorage.city;

const state = {
    city: defaultCity
}

const mutations = {
    changeCity(state,cityName) {
        state.city = cityName;
        localStorage.city = cityName;   // 本地存储
    }
}

export default new Vuex.Store({
    state,
    mutations

})


