<template>
    <div class="city">

        <City-Header></City-Header>
        <City-List 
        :cities="cities"
        :hotCities="hotCities"
        >
        </City-List>

    </div>
</template>

<script>
import CityHeader from './pages/Header'
import CityList from './pages/List'

export default {
    data () {
        return {
            hotCities: [],
            cities: {}
        }
    },

    mounted () {
        this.$axios.get("api/city.json")
        .then((res)=> {
            this.hotCities = res.data.data.hotCities;
            this.cities = res.data.data.cities;
        })
    },

    components: {
        CityHeader,
        CityList
    }
}
</script>

<style scoped>


</style>



