<template>
    <div class="hot">
        <div class="hot-top">
            <img src="http://img1.qunarzz.com/piao/fusion/1711/16/bfbb9874e8f11402.png" alt="">
            <span>本周热门榜单</span>
            <span class="hot-right">全部榜单 ></span>
        </div>

        <ul class="hot-lsit">
            <li class="hot-item"
                v-for="item in hotList" :key="item.id"
            >
                <img :src="item.imgUrl" alt="">
                <p>{{ item.title }}</p>
                <p><span class="hot-mark">￥{{ item.mark }}</span>起</p>
            </li>
        </ul>

    </div>
</template>

<script>
export default {
    props: ["hotList"],
    data () {
        return {
            
        }
    }
}
</script>

<style scoped lang="stylus">
@import '~css/common.styl'

.hot {
    font-size: .28rem;
    background: #fff;
    margin-top: .2rem;
}

.hot-top {
    padding: .2rem;
    position: relative;
}

.hot-top img {
    width: .3rem;
    height: .3rem;
}

.hot-top span {
    color: #212121;
    font-size: .32rem;
}

.hot-top .hot-right {
    position: absolute;
    right: .2rem;
    color: #616161;
    font-size: .28rem;
}

.hot-lsit {
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    height: 3.3rem;
}

.hot-item {
    display: inline-block;
    padding: 0 .2rem;
    width: 2rem;
    height: 2rem;
}

.hot-item img {
    width: 100%;
    height: 100%;
}

.hot-item p {
    margin-top: .1rem;
    text-align: center;
    textOverflow();
}

.hot-item p .hot-mark {
    color: #ff8300;
    font-size: .32rem;
}


</style>



