<template>
    <div class="banner">

        <div class="banner-img"
            @click='showSwiper'        
        >
            <div>
                <img src="http://img1.qunarzz.com/sight/p0/1609/7a/7ae8ee7831836095a3.water.jpg_600x330_5d562f69.jpg" alt="">
            </div>
            <div class="banner-title">
                金海湖风景区(AAAA景区)
            </div>
        </div>

        <div class="img-swiper"
            v-show='imgSwiper'
            @click='hiddenSwiper'
        >
            
            <swiper :options='swiperOption'>
                <swiper-slide
                    v-for="item in bannerList"
                    :key="item.id"
                >
                    <img :src="item.imgUrl" alt="">
                </swiper-slide>             
            </swiper>

			<div class="swiper-pagination"  slot="pagination"></div>  

        </div>

    </div>
</template>

<script>
export default {
    data () {
        return {
            swiperOption: {
                pagination: {
                    el: '.swiper-pagination',
                    type: 'fraction'
                }
            },
            bannerList: [
				{
					id:"01",
					imgUrl:"http://img1.qunarzz.com/sight/p0/1609/e4/e45d9cb483478459a3.water.jpg_r_800x800_a044eb00.jpg"
				},
				{
					id:"02",
					imgUrl:"http://img1.qunarzz.com/sight/p0/1609/e4/e45d9cb483478459a3.water.jpg_r_800x800_a044eb00.jpg"
				},
				{
					id:"03",
					imgUrl:"http://img1.qunarzz.com/sight/p0/1609/e4/e45d9cb483478459a3.water.jpg_r_800x800_a044eb00.jpg"
				}                
            ],
            imgSwiper: false
        }
    },
    methods: {
        showSwiper() {
            this.imgSwiper = true;
        },
        hiddenSwiper() {
            this.imgSwiper = false;
        }
    }
}
</script>

<style scoped>
.banner {
    position: relative;
}

.banner-img {
    width: 100%;
    height: 0;
    padding-bottom: 55%;
    overflow: hidden;
}

.banner-img img {
    width: 100%;
}

.banner-title {
    font-size: .36rem;
    color: #fff;
    bottom: .2rem;
    position: absolute;
    left: .3rem;
}

.img-swiper {
    background: #000;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    top: 0;
    bottom: 0;
}

.img-swiper img {
    width: 100%;
}

.swiper-pagination-fraction, 
.swiper-pagination-custom, 
.swiper-container-horizontal > 
.swiper-pagination-bullet {
    color: #fff;
    font-size: .32rem;
    bottom: .6rem;
}

</style>



