import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

import Home from '@/components/home/Home'
import City from '@/components/city/City'
import Detail from '@/components/details/Detail'


export default new Router({
  routes: [
    {
      path: '/',
      component: Home
    },
    {
      path: '/city',
      component: City
    },  
    {
      path: '/detail',
      component: Detail
    }   
  ]
})



