<template>

<div ref="container" class="container">
    <div>

        <!-- hot -->
        <div class="hot">
            <div class="hot-title">热门城市</div>
            <ul class="hot-list">
                <li class="hot-item"
                    v-for="item in hotCities"
                    :key="item.id"    
                    @click='changeCityName(item.name) '             
                >
                    {{ item.name }}
                </li>     
            </ul>
        </div>

        <!-- sort -->
        <div class="sort">
            <div class="sort-title">字母排序</div>
            <ul class="sort-list">
                <li class="sort-item"
                    v-for="(val,key) in cities"
                    :key="key"
                    @click='changeSort(key)'                      
                >
                    {{ key }}
                </li>                                                                                             
            </ul>
        </div>        

        <!-- list -->
        <div class="list">
            <!-- :ref绑定的是鼠标点击字母跳转的目标位置 -->
            <div
                v-for="(val,key) in cities"
                :key="key"
                :ref="key"
            >
                <div class="list-title"> {{ key }} </div>
                <ul class="list-msg">
                    <li class="list-item"
                        v-for="item in val"
                        :key="item.id"
                        @click='changeCityName(item.name) '                          
                    >
                        {{ item.name }}   
                    </li>          
                </ul>
            </div>
        </div>   

    </div>
</div>



</template>

<script>
import BScroll from 'better-scroll'
import { mapMutations } from 'vuex'

export default {
    props: ['cities','hotCities'],
    data () {
        return {
            scroll: ''
        }
    },

    mounted() {
        let container = this.$refs['container'];
        this.scroll = new BScroll(container);
    },

    methods: {
        // http://ustbhuangyi.github.io/better-scroll/doc/api.html
        changeSort(sortName) {
            this.scroll.scrollToElement(this.$refs[sortName][0])
        },

        // 点击城市名将值传递到首页
        changeCityName(cityName) {
            this.changeCity(cityName);
            this.$router.push('/')
        },

        ...mapMutations(['changeCity'])
    }


}
</script>

<style scoped lang="stylus">
@import '~css/common.styl';

.container {
    position: absolute;
    overflow hidden;
    left: 0;
    right: 0;
    bottom: 0;
    top: .88rem;
    background: #f5f5f5;
}

.hot {
    color: #212121;
}

.hot-title {
    font-size: .32rem;
    padding: .2rem .3rem;
}

.hot-list {
    font-size: .28rem;
    background: #fff;
    position: relative;
    overflow: hidden;
}

.hot-list:before {
    content: "";
    position: absolute;
    height: 100%;
    width: 33.33333%;
    left: 33.33333%;
    border-left: .02rem solid #ddd;
    border-right: .02rem solid #ddd;    
}

.hot-item {
    float: left;
    width: 33.33333%;
    height: .9rem;
    line-height: .9rem;
    text-align: center;
    border-bottom: .02rem solid #ddd;
    position relative;  /* 解决before覆盖item内容的问题 */
}

.sort {
    color: #212121;
}

.sort-title {
    font-size: .32rem;
    padding: .2rem .3rem;
}

.sort-list {
    font-size: .28rem;
    background: #fff;
    overflow: hidden;
}

.sort-item {
    float: left;
    width: 16.66666%;
    height: .9rem;
    line-height: .9rem;
    text-align: center;
}

.list {
    color: #212121;
}
.list-title {
    font-size: .32rem;
    padding: .2rem .3rem;
}

.list-msg {
    background: #fff;
    overflow: hidden;
    position: relative;
}

.list-msg:before {
    content: " ";
    width: 25%;
    height: 100%;
    position: absolute;
    left: 25%;
    border-left: .02rem solid #ddd;
    border-right: .02rem solid #ddd;
}

.list-msg:after {
    content: " ";
    width: 0;   /* 解决after覆盖的问题 */ 
    height: 100%;
    position: absolute;
    left: 75%;
    border-left: .02rem solid #ddd;
    border-right: .02rem solid #ddd;
}

.list-item {
    font-size: .28rem;
    width: 25%;
    height: .9rem;
    line-height: .9rem;
    text-align: center;
    float: left;
    border-bottom: .02rem solid #ddd;
    textOverflow();
    position: relative; /* 解决before覆盖的问题 */
}


</style>




