<template>
    <div class="vacation">
        <div class="vacation-title">
            周末去哪儿
        </div>

        <ul>
            <li class="vacation-item"
                v-for="item in vacationList" :key="item.id"
            >
                <div class="vacation-img">
                    <img :src="item.imgUrl" alt="">
                </div>
                <div class="vacation-text">
                    <p>{{ item.title }}</p>
                    <p>{{ item.msg }}</p>
                </div>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props:["vacationList"],
    data () {
        return {
            
        }
    }
}
</script>

<style scoped lang="stylus">
@import '~css/common.styl';

.vacation-title {
    font-size: .32rem;
    padding: .24rem;
}

.vacation-item {
    font-size: .28rem;
    margin-bottom: .1rem;
    background: #fff;
}

.vacation-img {
    width: 100%;
    height: 0;
    padding-bottom: 37.4375%;
}

.vacation-img img {
    width: 100%;
}

.vacation-text {
    padding: .1rem 0 .2rem .2rem;
}

.vacation-text p {
    margin-top: .1rem;
    textOverflow();
}

.vacation-text p+p {
    color: #616161;
    font-size: .2rem;
    margin-top: .08rem;;
}

</style>



