<template>
    <div class="header">
        
        <div class="header-left">
            <span class="iconfont">&#xe731;</span>
        </div>
        <div class="header-search">
            <span class="iconfont">&#xe61f;</span>输入城市/景点/游玩主题
        </div>
        <div class="header-right">
            <router-link to="/city">
                {{ city }}<span class="iconfont">&#xe606;</span>
            </router-link>

        </div>

    </div>
</template>

<script>
import {mapState} from 'vuex'

export default {
    computed: {
        ...mapState(['city'])
    }
}
</script>

<style scoped lang='stylus'>
@import '~css/var.styl'

.header {
    width: 100%;
    line-height: .88rem;
    background: $bgColor;
    display: flex;
    color: #fff;
    font-size: .36rem;
}

.header-left {
    width: .4rem;
    padding: 0 .15rem;
    text-align: center;
    font-weight: bold;
}

.header-search {
    flex: 1;
    line-height: .6rem;
    background: #fff;
    margin: .14rem 0;
    border-radius: .06rem;
    color: #e4e7ea;
    padding-left: .1rem;
    font-size: .28rem;
}

.header-right {
    font-size: .28rem;
    padding: 0 .15rem;
}

.header-right a {
    color: #fff;    
}

</style>




